import { Route, Routes, useNavigate } from "react-router-dom";
import Footer from "./components/partials/Footer";
import Headers from "./components/partials/Header";
import Main from "./components/views/Main";
import Error from "./Error";
import About from "./components/views/About";
import Contact from "./components/views/Contact";
import Signup from "./components/views/Signup";
import Login from "./components/views/Login";
import { ToastContainer } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import Logout from "./components/views/Logout";
import UserProfilehome from "./components/views/UserProfile";
import UserProfile from "./components/contexts/UserProfile";
import { useEffect, useState } from "react";
import Userqrgenrater from "./components/contexts/Userqrgenrater";
import Home from "./components/contexts/Home";
import ForYou from "./components/contexts/ForYou";
import FollowingPage from "./components/contexts/FollowingPage";
import Messageshow from "./components/contexts/Messageshow";
import PasswordChanges from "./components/contexts/PasswordChange";
import ChangeUsername from "./components/contexts/ChangeUsername";
import EditProfile from "./components/contexts/EditProfile";
import ConnectionCheck from "./components/contexts/ConnectionCheck";
import Forgetpassword from "./components/partials/Forgetpassword";
import MessageshowProfie from "./components/contexts/MessageshowProfie";

function App() {
  const navigate = useNavigate();
  const [getdata, setData] = useState([]);



  const callaboutpage = async () => {
    try {
      const res = await fetch('/authdata', {
        method: "GET",
        headers: {
          'Content-Type': 'application/json',
        },
      })

      const data = await res.json();
      setData(data);
    }
    catch (error) {
      navigate('/login');

    }


  }



  useEffect(() => {

    callaboutpage();

  }, [])

  return (
    <>
      <Headers />
      {/* <ConnectionCheck> */}
        <ToastContainer />
        <Routes>
          <Route path="/" element={<Main />} >
            <Route path="/" element={<Home />} >
              <Route path="/home" element={<ForYou />} />
              <Route path="/home/following" element={<FollowingPage />} />
              <Route path={getdata.username} element={<MessageshowProfie />} />

            </Route>
          </Route>
          <Route path="/about" element={<About />} />
          <Route path="/contact" element={<Contact />} />
          <Route path="/signup" element={<Signup />} />
          <Route path="/forgetpassword" element={<Forgetpassword />} />
          <Route path="/login" element={<Login />} />
          <Route path='' element={<UserProfilehome />}>
            <Route path={getdata.username + "/profile"} element={<UserProfile />} />
            <Route path={getdata.username + "/profile/qrscanner"} element={<Userqrgenrater />} />
            <Route path={getdata.username + "/message"} element={<Messageshow />} />
            <Route path={getdata.username + "/changeusername"} element={<ChangeUsername />} />
            <Route path={getdata.username + "/editprofile"} element={<EditProfile />} />
            <Route path={getdata.username + "/changepassword"} element={<PasswordChanges />} />
            <Route path={getdata.username + "/profile/setting"} element={<UserProfile />} />
            <Route path={getdata.username + "/profile/delete"} element={<UserProfile />} />
            <Route path={getdata.username + "/logout"} element={<Logout />} />

          </Route>

          <Route path="/logout" element={<Logout />} />
          <Route path="*" element={<Error />} />
        </Routes>
      {/* </ConnectionCheck> */}
      <Footer />
    </>


  );
}

export default App;
