import React, { useEffect, useState } from 'react'
import './UserQrgenrater.css'
import LoaderprofileComponent from '../partials/LoaderprofileComponent';
import { useNavigate } from 'react-router-dom';
function Userqrgenrater() {
    const [qrCodeUrl, setsdata] = useState([]);
    const [getdata, setdata] = useState([])
    const navigate = useNavigate();
    const getdatauser = async () => {
        try {
            const res = await fetch('/authdata', {
                method: 'GET'
            })
            const data = await res.json();
            setdata(data);

        }
        catch (error) {
            navigate('/login');
        }
    }
    document.title=`algocoretech/${getdata.username}/qrcode`;

    const fetchqrcoderdata = async () => {
        try {
            const username = getdata.username;
            const inputData = `http://algocoretech.com/${username}`

            const res = await fetch('/userqrcode', {

                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ data: inputData }),
            })
            const data = await res.json()
            setsdata(data.qrCodeUrl)
        }
        catch (error) {

        }
    }
    useEffect(() => {
        getdatauser();
    }, []);
        fetchqrcoderdata();
    const [isLoading, setIsLoading] = useState(true);
    useEffect(() => {
        setTimeout(() => {
            setIsLoading(false);
        }, 800);
    }, []);


    return (
        <>
            {isLoading ? (
                <LoaderprofileComponent />
            ) : (

                <main className='main-userprofile-container'>
                    <div className="main-userprofile-box">
                        <div className="main-userprofile-heading-box">
                            <h1 className='main-userprofile-heading-text'>Your Profile QR</h1>
                        </div>
                        <div className="main-userprofile-qr-box">
                            <div className="qrbackground">
                                {qrCodeUrl && <img src={qrCodeUrl} alt="QR Code" />}
                            </div>
                        </div>

                    </div>

                </main>
            )}

        </>
    )
}

export default Userqrgenrater
