import React, { useEffect, useState } from 'react'
import './PostInputbox.css'
import { NavLink } from 'react-router-dom';
import { toast } from 'react-toastify'
function PostInputbox() {
    const [getdata, setdata] = useState([]);
    const [getinputdata, setinputdata] = useState({ message: "" })
    const getuserdata = async () => {
        try {
            const res = await fetch('/authdata', {
                method: 'GET'
            })
            const data = await res.json();
            setdata(data)

        }
        catch (error) {
            console.log("Some technical issue");
        }
    }
    useEffect(() => {
        getuserdata();
    }, [])

    const chageinputvalue = (event) => {
        let message = event.target.name;
        let value = event.target.value;
        setinputdata({ ...getinputdata, [message]: value })

    }
    const Datasendpost = async (event) => {
        const username = getdata.username
        event.preventDefault();
        const { message } = getinputdata;
        console.log(message)
        const res = await fetch('/message', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ message, username })
        })
        if (res.status === 200) {
            toast.success('Your Post Sucessfully send');
            window.location.reload();
        }
        else if (res.status === 500) {
            toast.error('Your message box blank');

        }
        else if (res.status === 404) {
            toast.error('Some technical issue');

        }

    }
    return (
        <>
            <label htmlFor="message-box-input">
                <main className='postinputbox-container'>
                    <div className="postinputbox-side-container">

                        <NavLink to="/home" className="user-details-box">
                            <span class="material-symbols-outlined icon-user">
                                account_circle
                            </span>
                        </NavLink>
                    </div>
                    <form className="postinputbox-left-container" method='post'>
                        <div className="postinputbox-left-top-container">
                            <textarea type="text" name='message' id='message-box-input' value={getinputdata.message} onChange={chageinputvalue} placeholder='What is happening?!' />
                        </div>
                        <div className="postinputbox-left-bottom-container">
                            <button type='submit' className='button-container-box' onClick={Datasendpost}>POST</button>
                        </div>

                    </form>
                </main>
            </label>
        </>
    )
}

export default PostInputbox
