import React from 'react'
import './LoaderprofileComponent.css'
function LoaderprofileComponent() {
  return (
    <>
       <div className="loader-profile-container">
            <div className="loader-con">
                <div className="loader"></div>
            </div>

            <div className="heading-con" ><h2  style={{color:"brown"}}>Fetching data, just a moment few second ..</h2></div>
        </div>
    </>
  )
}

export default LoaderprofileComponent
