import React, { useEffect, useState } from 'react'
import './Home.css'
import { NavLink, Outlet } from 'react-router-dom'
import LoadingHome from '../partials/LoadingHome';
function Home() {
    const [isloading, setloading] = useState(true);
    useEffect(() => {
        setTimeout(() => {
            setloading(false)
        }, 600)
    }, [])
    return (
        <>
            {isloading ? (<LoadingHome />) : (
                


                <main className='Home-main-container-data'>
                    <div className="Home-main-container-hedaing">
                        <div className="Home-main-containerbox">
                            <NavLink to='/home' className='homefollowing-link-box'>ForYou</NavLink>
                        </div>
                        <div className="Home-main-containerbox">
                            <NavLink to='/home/following' className='homefollowing-link-box'>Following</NavLink>
                        </div>
                    </div>
                    <Outlet/>
                   
                </main>
            )}
        </>
    )
}

export default Home
