import React, { useState, useEffect } from 'react'
import './UserProfile.css'
import {NavLink, useNavigate } from 'react-router-dom'
import LoaderprofileComponent from '../partials/LoaderprofileComponent'
function UserProfile() {
    const navigate = useNavigate();
    const [isLoading, setIsLoading] = useState(true);
    const [getdata, setdata] = useState([])
    useEffect(() => {
        setTimeout(() => {
            setIsLoading(false);
        }, 600);
    }, []);
    document.title=`algocoretech/${getdata.username}/profile`;

    const getdatauser = async () => {
        try {
            const res = await fetch('/authdata', {
                method: 'GET'
            })
            const data = await res.json();
            setdata(data);

        }
        catch (error) {
            navigate('/login');
        }
    }
    useEffect(() => {
        getdatauser();
    }, []);


    return (
        <>
            {isLoading ? (
                <LoaderprofileComponent />
            ) : (

                <main className='main-userprofiles-container'>
                    <div className="main-userprofile-boxs">
                        <div className="main-userprofile-heading-boxs">
                            <h1 className='main-userprofile-heading-text'>Your Profile</h1>
                        </div>
                        <div className="main-userprofile-cons">
                            <span className='main-userprofile-heading-text-name'>Name : </span><p className='value-name-data'>{getdata.name}</p>
                        </div>
                        <div className="main-userprofile-cons">
                            <span className='main-userprofile-heading-text-name'>Email : </span><p className='value-name-data'>{getdata.email}</p>
                        </div>
                        <div className="main-userprofile-cons">
                            <span className='main-userprofile-heading-text-name'>Phone : </span><p className='value-name-data'>{getdata.phone}</p>
                        </div>
                        <div className="main-userprofile-cons">
                            <span className='main-userprofile-heading-text-name'>Username : </span><p className='value-name-data'>{getdata.username}</p>
                        </div>
                        <div className="main-userprofile-cons">
                            <span className='main-userprofile-heading-text-name'>Address : </span><p className='value-name-data'>{getdata.address}</p>
                        </div>
                        <div className="main-userprofile-cons">
                            <span className='main-userprofile-heading-text-name'>District : </span><p className='value-name-data'>{getdata.district}</p>
                        </div>
                        <div className="main-userprofile-cons">
                            <span className='main-userprofile-heading-text-name'>Alternate Email : </span><p className='value-name-data'>{getdata.alternateemail}</p>
                        </div>
                        <div className="main-userprofile-cons">
                            <span className='main-userprofile-heading-text-name'>Pincode : </span><p className='value-name-data'>{getdata.pincode}</p>
                        </div>
                        <div className="main-userprofile-cons">
                            <span className='main-userprofile-heading-text-name'>Alternate Mobile no. : </span><p className='value-name-data'>{getdata.alternatephone}</p>
                        </div>
                        <div className="main-userprofile-button">
                                <NavLink to={'/'+getdata.username+'/changepassword'} className='userprofile-link'>Password Reset</NavLink>
                        </div>
                    </div>

                </main>
            )}
        </>
    )
}

export default UserProfile
