import React, { useEffect, useState } from 'react'
import LoaderComponent from '../partials/Loader';
import { NavLink, Outlet, useNavigate } from 'react-router-dom';
import './Main.css'
function Main() {
  document.title="algocoretech/home";

  const [isLoading, setIsLoading] = useState(true);
  const [getdata, setdata] = useState([]);
  const navigate = useNavigate();
  useEffect(() => {
    setTimeout(() => {
      setIsLoading(false);
    }, 600);
  }, []);


  const authuser = async () => {
    try {

      const res = await fetch('/authdata', {
        method: 'GET',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json'
        },
        credentials: "include"
      })
      const data = await res.json()
      setdata(data)

    }
    catch (error) {
      navigate('/login');
    }

  }

  useEffect(() => {
    authuser();

  }, []);
  return (
    <>
      {isLoading ? (
        <LoaderComponent />
      ) : (
        <main className='Main-container'>
          <div className="mainside-left-container">
            <div className="mainside-left-box-container">
              <div className="mainsidebox topboxhome">
                <NavLink to='/home' className='navlink-box'>
                  <span class="material-symbols-outlined   iconhomeside">
                    home
                  </span>
                  <h2>Home</h2>
                </NavLink>

              </div>
              <div className="mainsidebox ">
                <NavLink to='/' className='navlink-box'>
                  <span class="material-symbols-outlined   iconhomeside">
                    search
                  </span>
                  <h2>Explore</h2>
                </NavLink>

              </div>
              <div className="mainsidebox ">
                <NavLink to='/' className='navlink-box'>
                  <span class="material-symbols-outlined   iconhomeside">
                    notifications
                  </span>
                  <h2>Notifications</h2>
                </NavLink>

              </div>
              <div className="mainsidebox ">
                <NavLink to={'/'+getdata.username} className='navlink-box'>
                  <span class="material-symbols-outlined   iconhomeside">
                    mail
                  </span>
                  <h2>Messages</h2>
                </NavLink>

              </div>
              <div className="mainsidebox ">
                <NavLink to='/' className='navlink-box'>
                  <span class="material-symbols-outlined   iconhomeside">
                    contract
                  </span>
                  <h2>Lists</h2>
                </NavLink>

              </div>
              <div className="mainsidebox ">
                <NavLink to='/' className='navlink-box'>
                  <span class="material-symbols-outlined   iconhomeside">
                    group
                  </span>
                  <h2>Communities</h2>
                </NavLink>

              </div>
              <div className="mainsidebox ">
                <NavLink to='/' className='navlink-box'>
                  <span class="material-symbols-outlined   iconhomeside">
                    verified
                  </span>
                  <h2>Verified</h2>
                </NavLink>

              </div>
              <div className="mainsidebox ">
                <NavLink to={getdata.username + '/profile'} className='navlink-box'>
                  <span class="material-symbols-outlined   iconhomeside">
                    person
                  </span>
                  <h2>Profile</h2>
                </NavLink>

              </div>

              <div className="mainsidebox ">
                <NavLink to='/' className='navlink-box lasthomeuser'>
                  <h2>Post</h2>

                </NavLink>
              </div>
              <div className="mainsidebox ">

                <NavLink to="/home" className=" navlink-box lasthomeuser">
                  <div className="user-icon user-details-boxs">
                    <span class="material-symbols-outlined icon-user">
                      account_circle
                    </span>
                  </div>
                  <div className="user-details-name-boxs">
                   <div className="user-details-name">{getdata.name}</div>
                   <div className="user-details-username">{getdata.username}</div>
                  </div>

                </NavLink>

              </div>
            </div>


          </div>












          <div className="mainside-middile-container">
            <Outlet />
          </div>
          <div className="mainside-right-container">
          </div>
        </main>
      )}
    </>
  )
}

export default Main
