import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
function Forgetpassword() {
    let navigate = useNavigate();
    const [getdata, setdata] = useState({ email: "", newpassword: "", conformpassword: "" });
    const changedata = (event) => {
        let name = event.target.name;
        let value = event.target.value;
        setdata({ ...getdata, [name]: value })
    }
    const submitdetails = async (event) => {
        event.preventDefault();
        try {
            const { email, newpassword, conformpassword } = getdata;
            const res = await fetch('/forgetpassword', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ email, newpassword, conformpassword })
            })
            if (res.status === 200) {
                toast.success('Password Forget Sucessfully');
                navigate('/login');
                window.location.reload();
            }
            else if (res.status === 409) {
                toast.error('New password and conform password are not matched');

            }
            else if (res.status === 408) {
                toast.error('Email id not register');

            }
            else if (res.status === 500) {
                toast.error('All field require');

            }
            else if (res.status === 403) {
                toast.error('Some technical issue');

            }

        }
        catch (error) {
            toast.error("This time sum technical issues");
        }
    }
    return (
        <>
            <main className='login-main-container'>
                <div className="login-container">
                    <div className="login-heading">
                        <h1 className='pagename'>Password Forget</h1>
                    </div>
                    <div className="login-input">
                        <form action="/" className="login-input-form" method="post">
                            <input
                                type="email"
                                name="email"
                                id="email"
                                className="input-field"
                                placeholder="Email"
                                value={getdata.name}
                                onChange={changedata}

                            /><br />
                            <input
                                type="password"
                                name="newpassword"
                                id="password"
                                className="input-field"
                                placeholder="New Password"
                                value={getdata.newpassword}
                                onChange={changedata}

                            /><br />
                            <input
                                type="text"
                                name="conformpassword"
                                id="password"
                                className="input-field"
                                placeholder="Conform Password"
                                value={getdata.conformpassword}
                                onChange={changedata}

                            /><br />
                            <div className="submit-container">
                                <input type="submit" id="submit" value="Forget Password" onClick={submitdetails} /><br />
                            </div>
                            <div className="usernot-login-container">
                                <p style={{ color: "white", fontWeight: 700 }}>Dont have an account?</p>
                                <Link to='/signup' className='signup-link'>Sign up</Link>
                            </div>


                        </form>
                    </div>
                </div>
            </main>

        </>
    )
}

export default Forgetpassword
