import React, { useEffect, useState } from 'react'
import './Messageshow.css'
import LoaderprofileComponent from '../partials/LoaderprofileComponent';

function Messageshow() {
    const [isloading, setloading] = useState(true);
    useEffect(() => {
        setTimeout(() => {
            setloading(false)

        }, 700)

    }, [])
    return (
        <>
            {isloading ? (<LoaderprofileComponent />) : (

                <main className='mainmessage-show-box-container'>
                    <div className="mainmessage-show-message-container">
 
                    </div>

                </main>
            )

            }
        </>
    )
}

export default Messageshow
