import React, { useEffect, useState } from 'react'
import LoaderprofileComponent from '../partials/LoaderprofileComponent'
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import './EditProfile.css'
function EditProfile() {
    const [isloading, setloading] = useState(true);
    const navigate = useNavigate();
    const [usergetdata, usersetdata] = useState([]);
    const [getvalue, setvalue] = useState({ email: usergetdata.email, address: usergetdata.address, district: usergetdata.district, pincode: usergetdata.pincode, phone: usergetdata.phone, alternateemail: usergetdata.alternateemail, alternatephone: usergetdata.alternatephone });
    setTimeout(() => {
        setloading(false)

    }, 700)

    document.title=`algocoretech/${usergetdata.username}/edit/profile`;


    const changevalue = (event) => {
        let email = event.target.name;
        let value = event.target.value;
        setvalue({ ...getvalue, [email]: value });

    }
    const getuserdatas = async () => {
        try {
            const res = await fetch('/authdata', {
                method: 'GET'
            })
            const data = await res.json();
            usersetdata(data)
        }
        catch (error) {
            toast.error('Some technical issue');
        }
    }

    useEffect(() => {
        getuserdatas();
    }, [])
    const datasubmit = async (event) => {
        event.preventDefault();
        try {
            const { address, district, pincode, phone, alternateemail, alternatephone } = getvalue;
            const id = usergetdata._id;
            const res = await fetch(`/profile/update/${id}`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ address, district, pincode, phone, alternateemail, alternatephone })
            })
            if (res.status === 200) {
                toast.success('UserName Update Sucessfully');
                navigate('/'+usergetdata.username+"/profile");
                window.location.reload()
            }
            else if (res.status === 500) {
                toast.error('All field require');
            }
            else if (res.status === 407) {
                toast.error('Some technical issue');
            }
        }
        catch (error) {
            toast.error('Some technical issue');

        }


    }

    return (
        <>
            {isloading ? (<LoaderprofileComponent />) : (

                <main className='mainpassword-show-box-container'>
                    <div className="mainpassword-show-edit-container">
                        <div className="heading-box-con">
                            <h1>Edit Profile </h1>
                        </div>
                        <form action="" method='put'>
                            <input type="text" className='inputvalue-box-password' name='email' value={usergetdata.email} id='emailid-changepassword' />
                            <input type="number" className='inputvalue-box-password' maxLength='10' name='phone' placeholder='Phone' value={getvalue.phone} onChange={changevalue} />
                            <input type="text" className='inputvalue-box-password' name='address' placeholder='Address' value={getvalue.address} onChange={changevalue} />
                            <input type="text" className='inputvalue-box-password' name='district' placeholder='District' value={getvalue.district} onChange={changevalue} />
                            <input type="email" className='inputvalue-box-password' name='alternateemail' placeholder='Alternate Email' value={getvalue.alternateemail} onChange={changevalue} />
                            <input type="phone" className='inputvalue-box-password' name='alternatephone' placeholder='Alternate Phone' value={getvalue.alternatephone} onChange={changevalue} />
                            <input type="tel" maxLength={6} className='inputvalue-box-password' name='pincode' placeholder='Pin code' value={getvalue.pincode} onChange={changevalue} />
                            <div className="submitbox-container">
                                <input type="submit" value="Change Password" onClick={datasubmit} id='inputvalue-box-button' />

                            </div>
                        </form>
                    </div>

                </main>
            )

            }
        </>
    )
}

export default EditProfile
