import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import './Signup.css';
import { toast } from 'react-toastify';
function Signup() {
  document.title="algocoretech/signup";

  let navigate = useNavigate();
  const [getvalue, setvalue] = useState({ name: "", email: "", phone: "", username: "", password: "", conformpassword: "" })
  const changedata = (event) => {
    let name = event.target.name;
    let value = event.target.value;
    setvalue({ ...getvalue, [name]: value })
  }
  const submitdetails = async (event) => {
    event.preventDefault();
    try {
      const { name, email, phone, username, password, conformpassword }=getvalue
      const res = await fetch('/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ name, email, phone, username, password, conformpassword })
      })
      if (res.status === 200) {
        toast.success('Registration Sucessfully');
        navigate('/login');
      }
      else if (res.status === 500) {
        toast.error('All field require');
      }
      else if (res.status === 405) {
        toast.error('Password and conform password are not mached');
       
      }
      else if (res.status === 401) {
        toast.error('Email id already exist');
       
      }
      else if (res.status === 406) {
        toast.error('Phone number already exist');
       
      }
      else if (res.status === 408) {
        toast.error('User name already exist');
       
      }
      else if (res.status === 403) {
        toast.error('Some technical issue');
       
      }

    }
    catch (error) {
      toast.error('this time some technical issues');

    }


  }
  return (
    <>
      <main className='signup-main-container'>
        <div className="signup-m-container">
          <div className="leftside-container">
          </div>

          <div className="rightside-input">
            <div className="signup-heading-container">
              <h1>Signup</h1>
            </div>
            <form action="/signup" className="signup-input-form-container" method="post">
              <input
                type="text"
                name="name"
                id="name"
                className="input-fields"
                placeholder="Name"
                value={getvalue.name}
                onChange={changedata}
              />
              <input
                type="email"
                name="email"
                id="email"
                className="input-fields"
                placeholder="Email"
                value={getvalue.email}
                onChange={changedata}


              />
              <input
                type="number"
                name="phone"
                id="phone"
                className="input-fields"
                placeholder="Phone"
                value={getvalue.phone}
                onChange={changedata}


              />

              <input
                type="text"
                name="username"
                id="username"
                className="input-fields"
                placeholder="Username"
                value={getvalue.username}
                onChange={changedata}


              />

              <input
                type="password"
                name="password"
                id="password"
                className="input-fields"
                placeholder="Password"
                value={getvalue.password}
                onChange={changedata}


              />
              <input
                type="text"
                name="conformpassword"
                id="conformpassword"
                className="input-fields"
                placeholder="Conformpassword"
                value={getvalue.conformpassword}
                onChange={changedata}


              />
              <div className="submit-container">
                <input type="submit" id="submit" value="submit" onClick={submitdetails} /><br />
              </div>
              <div className="loginpage-container">
                <p className='name-login'>Have an account?</p>
                <Link to='/login' className='login-page-name'>Log in</Link>
              </div>
            </form>

          </div>
        </div>
      </main>
    </>
  )
}

export default Signup