import React, { useState,useEffect } from 'react'
import LoadinghomeHome from '../partials/loadinghomeHome'
import PostInputbox from '../partials/PostInputbox';
function FollowingPage() {
    const [isloading, setloading] = useState(true);
    useEffect(()=>{
        setTimeout(()=>{
          setloading(false);
      
        },1000)
      
      },[])
    return (
        <>
            {isloading ? (<LoadinghomeHome />) : (
               <PostInputbox/>

            )}

        </>
    )
}

export default FollowingPage
