import React, { useState, useEffect } from 'react'
import LoaderprofileComponent from '../partials/LoaderprofileComponent'
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
function ChangeUsername() {
    const [isloading, setloading] = useState(true);
    const navigate = useNavigate();
    const [getvalue, setvalue] = useState({ email: "", username: "", newusername: '' });
    const [usergetdata, usersetdata] = useState([]);
    setTimeout(() => {
        setloading(false)

    }, 700)

    document.title = `algocoretech/${usergetdata.username}/change/username`;


    const changevalue = (event) => {
        let email = event.target.name;
        let value = event.target.value;
        setvalue({ ...getvalue, [email]: value });

    }
    const getuserdatas = async () => {
        try {
            const res = await fetch('/authdata', {
                method: 'GET'
            })
            const data = await res.json();
            usersetdata(data)
        }
        catch (error) {
            toast.error('Some technical issue');
        }
    }

    useEffect(() => {
        getuserdatas();
    }, [])
    const datasubmit = async (event) => {
        event.preventDefault();
        try {
            const { username, newusername } = getvalue;
            const id = usergetdata._id;
            const res = await fetch(`/changeusername/${id}`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ username, newusername })
            })
            if (res.status === 200) {
                toast.success('UserName Update Sucessfully');
                navigate('/');
                window.location.reload()
            }
            else if (res.status === 403) {
                toast.error('User name already exist ');
            }
            else if (res.status === 406) {
                toast.error('Username are not matched pleased valid user name');
            }
            else if (res.status === 500) {
                toast.error('All field require');
            }
            else if (res.status === 407) {
                toast.error('Some technical issue');
            }
        }
        catch (error) {
            toast.error('Some technical issue');

        }


    }


    return (
        <>
            {isloading ? (<LoaderprofileComponent />) : (

                <main className='mainpassword-show-box-container'>
                    <div className="mainpassword-show-message-container">
                        <div className="heading-box-con">
                            <h1>Change UserName</h1>
                        </div>
                        <form action="" method='put'>
                            <input type="text" className='inputvalue-box-password' name='email' value={usergetdata.email} id='emailid-changepassword' />
                            <input type="text" className='inputvalue-box-password' name='username' placeholder='Username' value={getvalue.username} onChange={changevalue} />
                            <input type="text" className='inputvalue-box-password' name='newusername' placeholder='New Username' value={getvalue.newusername} onChange={changevalue} />
                            <div className="submitbox-container">
                                <input type="submit" value="Change Password" onClick={datasubmit} id='inputvalue-box-button' />

                            </div>
                        </form>
                    </div>

                </main>
            )

            }
        </>
    )
}

export default ChangeUsername
