import React, { useEffect, useState } from 'react'
import './Passwordchange.css'
import LoaderprofileComponent from '../partials/LoaderprofileComponent';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';
function PasswordChanges() {
    const navigate = useNavigate();
    const [isloading, setloading] = useState(true);
    const [getvalue, setvalue] = useState({ email: "", password: "", newpassword: "", newconformpassword: "" });
    const [usergetdata, usersetdata] = useState([]);
    setTimeout(() => {
        setloading(false)

    }, 700)


    const changevalue = (event) => {
        let email = event.target.name;
        let value = event.target.value;
        setvalue({ ...getvalue, [email]: value });

    }
    const getuserdatas = async () => {
        try {
            const res = await fetch('/authdata', {
                method: 'GET'
            })
            const data = await res.json();
            usersetdata(data)
        }
        catch (error) {
            toast.error('Some technical issue');
        }
    }

    useEffect(() => {
        getuserdatas();
    }, [])
    document.title=`algocoretech/${usergetdata.username}/changepassword`;

    const datasubmit = async (event) => {
        event.preventDefault();
        try {
            const { password, newpassword, newconformpassword } = getvalue;
            const res = await fetch(`/changepassword/${usergetdata._id}`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ password, newpassword, newconformpassword })
            })
            if (res.status === 200) {
                toast.success("Password Update Sucessfully Done");
                navigate('/logout');
                window.location.reload();
            }
            else if (res.status === 500) {
                toast.error('All field require');
            }
            else if (res.status === 406) {
                toast.error('Password Woring Pleased given valid password');
            }
            else if (res.status === 408) {
                toast.error('New password and New conformpassword are not matched');
            }

        }
        catch (error) {
            toast.error('Some technical issue');
        }


    }
    return (
        <>
            {isloading ? (<LoaderprofileComponent />) : (

                <main className='mainpassword-show-box-container'>
                    <div className="mainpassword-show-message-container">
                        <div className="heading-box-con">
                            <h1>Change Password</h1>
                        </div>
                        <form action="" method='put'>
                            <input type="text" className='inputvalue-box-password' name='email' value={usergetdata.email} id='emailid-changepassword' />
                            <input type="password" className='inputvalue-box-password' name='password' placeholder='Password' value={getvalue.password} onChange={changevalue} />
                            <input type="text" className='inputvalue-box-password' name='newpassword' placeholder='New Password' value={getvalue.newpassword} onChange={changevalue} />
                            <input type="text" className='inputvalue-box-password' name='newconformpassword' placeholder='New Conform Password' value={getvalue.newconformpassword} onChange={changevalue} />
                            <div className="submitbox-container">
                                <input type="submit" value="Change Password" onClick={datasubmit} id='inputvalue-box-button' />

                            </div>
                        </form>
                    </div>

                </main>
            )

            }
        </>
    )
}

export default PasswordChanges
