import React, { useEffect, useState } from 'react'
import LoadinghomeHome from '../partials/loadinghomeHome'
import PostInputbox from '../partials/PostInputbox';
import { toast } from 'react-toastify';
import './ForYou.css'
import { NavLink } from 'react-router-dom';
function ForYou() {
  const [isloading, setloading] = useState(true);
  const [getdata, setdata] = useState([]);
  useEffect(() => {
    setTimeout(() => {
      setloading(false);

    }, 600)

  }, [])

  const getallmessageuser = async () => {
    try {
      const res = await fetch('/getallmessage', {
        method: 'GET'
      })
      const data = await res.json();
      setdata(data);
    }
    catch (error) {
      toast.error('Some technical issue')
    }
  }

  useEffect(() => {
    getallmessageuser();

  }, [])

  const [getlike, setlike] = useState()


  const likeget = async (id, like) => {
    setlike(like + 1);
    try {

      const res = await fetch(`/getallmessage/${id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ getlike })

      })


    }
    catch (error) {
    }



  }
  return (
    <>

      {isloading ? (<LoadinghomeHome />) : (
        <main className='main-user-twite-details'>
          <PostInputbox />
          {
            getdata.map((element, index) => (

              <div className="message-show-box-container">
                <div className="sideprofile-image-container">
                  <NavLink className="sideprofile-image"></NavLink>
                </div>
                <div className="userdetails-username-more-container">
                  <div className="userdetails-username-more-box">
                    <NavLink to={'/' + element.username} className='navlink'>
                      <p key={index} className='username-show'>{element.username}</p>

                    </NavLink>
                    {/* <p key={index}>{element.createdAt}</p> */}
                    <div className="containerthreedot">
                      <span class="material-symbols-outlined threedot">
                        more_horiz
                      </span>
                    </div>
                  </div>

                  <div className="message-user-container">
                    <p className='message-user'>{element.message}</p>
                  </div>

                  <div className="footer-box-container">
                    <div className="comment-post-container first">
                      <span class="material-symbols-outlined iconsend">
                        quick_phrases
                      </span>
                      <p className='number-show'>0</p>

                    </div>
                    <div className="like-post-container">
                      <span class="material-symbols-outlined iconsend" onClick={() => { likeget(element._id, element.like) }}  >
                        favorite
                      </span>
                      <p className='number-show'>{element.like}</p>

                    </div>
                    <div className="share-post-container">
                      <span class="material-symbols-outlined iconsend">
                        ios_share
                      </span>
                      <p className='number-show'>0</p>

                    </div>
                    <div className="view-post-container">
                      <span class="material-symbols-outlined iconsend">
                        visibility
                      </span>
                      <p className='number-show'>0</p>
                    </div>
                  </div>
                </div>
              </div>

            ))

          }


        </main>

      )}
    </>
  )
}
export default ForYou
