import React, { useEffect, useState } from 'react'
import LoaderComponent from '../partials/Loader';
import { Link, Outlet, useNavigate } from 'react-router-dom';
import './UserProfile.css'
import { toast } from 'react-toastify';
function UserProfile() {


    const navigate = useNavigate();
    const [isLoading, setIsLoading] = useState(true);
    const [getdata, setdata] = useState([]);
    document.title=`algocoretech/${getdata.username}/profile`;
    const authuserprofile = async () => {
        try {

            const res = await fetch('/authdata', {
                method: 'GET',
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json'
                },
                credentials: "include"
            })
            const data = await res.json()
            setdata(data);

        }
        catch (error) {
            navigate('/login');
        }

    }
    const deletefunction = async (id) => {
        try {
            const res = await fetch(`/profile/delete/${id}`, {
                method: 'DELETE'
            })
            if (res.status === 200) {
                toast.success('Account delete Sucessfully');
                navigate('/signup');
                window.location.reload();
            }

        }
        catch (error) {
 console.log('Some technical issue')
        }


    }

    useEffect(() => {
        setTimeout(() => {
            setIsLoading(false);
        }, 600);
        authuserprofile();

    }, [])
    return (
        <>
            {isLoading ? (
                <LoaderComponent />
            ) : (

                <main className='main-class-container'>
                    <div className="main-class-box-container">
                        <div className="class-box-left-container">
                            <div className="class-box-container">
                                <div className="class-box-userdetails topbox">
                                    <Link to={getdata.username + "/profile"} className="class-link-userdetails">
                                        <span class="material-symbols-outlined icon-details">
                                            manage_accounts
                                        </span>


                                        <h2 className='text-userprofile'>Profile</h2>
                                    </Link>

                                </div>
                                <div className="class-box-userdetails">
                                    <Link to={getdata.username + "/profile/qrscanner"} className="class-link-userdetails">
                                        <span class="material-symbols-outlined icon-details">
                                            qr_code_2
                                        </span>
                                        <h2 className='text-userprofile'>Profile Qrcode</h2>
                                    </Link>

                                </div>
                                <div className="class-box-userdetails" >
                                    <Link to={getdata.username + "/message"} className="class-link-userdetails">
                                        <span class="material-symbols-outlined icon-details">
                                            mark_unread_chat_alt
                                        </span>
                                        <h2 className='text-userprofile'>Message</h2>
                                    </Link>

                                </div>

                                <div className="class-box-userdetails">
                                    <Link to={getdata.username + "/changeusername"} className="class-link-userdetails">
                                        <span class="material-symbols-outlined icon-details">
                                            person_add
                                        </span>
                                        <h2 className='text-userprofile'>Username Change </h2>
                                    </Link>

                                </div>
                                <div className="class-box-userdetails">
                                    <Link to={getdata.username + "/editprofile"} className="class-link-userdetails">
                                        <span class="material-symbols-outlined icon-details">
                                            edit_note
                                        </span>
                                        <h2 className='text-userprofile'>Edit Profile</h2>
                                    </Link>

                                </div>
                                <div className="class-box-userdetails">
                                    <Link to={getdata.username + "/changepassword"} className="class-link-userdetails">
                                        <span class="material-symbols-outlined icon-details">
                                            lock_reset
                                        </span>
                                        <h2 className='text-userprofile'>Password change</h2>
                                    </Link>

                                </div>


                                <div className="class-box-userdetails">
                                    <Link to={getdata.username + "/profile/setting"} className="class-link-userdetails">
                                        <span class="material-symbols-outlined icon-details">
                                            settings
                                        </span>
                                        <h2 className='text-userprofile '>Setting</h2>
                                    </Link>

                                </div>
                                <div className="class-box-userdetails">
                                    <Link to={getdata.username + "/profile/logout"} className="class-link-userdetails">
                                        <span class="material-symbols-outlined icon-details">
                                            logout
                                        </span>
                                        <h2 className='text-userprofile'>Logout</h2>
                                    </Link>

                                </div>
                                <div className="class-box-userdetails">
                                    <Link to={getdata.username + "/profile/delete"} onClick={() => { deletefunction(getdata._id) }} className="class-link-userdetails">
                                        <span class="material-symbols-outlined icon-details deletprofile">
                                            delete
                                        </span>
                                        <h2 className='text-userprofile deletprofile' >Delete Profile</h2>
                                    </Link>

                                </div>
                            </div>

                        </div>
                        <Outlet />
                    </div>
                </main>
            )}

        </>
    )
}

export default UserProfile
